я ИБ можно определить, что под информационной угрозой понимается воздействие дестабилизирующих факторов на состояние информированности, подвергающее опасности жизненно важные интересы личности, общества и государства."),_c('br'),_vm._v(" В законе РБ «О безопасности» дано определение угрозы безопасности как совокупности условий, факторов, создающих опасность жизненно важным интересам личности, общества и государства. Под угрозой информации в системах ее обработки понимается возможность возникновения на каком -либо этапе жизнедеятельности системы такого явления или события, следствием которого могут быть нежелательные воздействия на информацию. К настоящему времени известно большое количество разноплановых угроз различного происхождения, таящих в себе различную опасность для информации. Для системного представления их удобно классифицировать по виду, возможным источникам, предпосылкам появления и характеру проявления."),_c('br'),_vm._v(" Виды угроз Определив понятие «угроза государству, обществу и личности» в широком смысле, рассмотрим его относительно не посредственного воздействия на конфиденциальную информацию, обрабатываемую на каком-либо объекте (кабине те, предприятии, фирме). Анализируя возможные пути воздействия на информацию, представляемую как совокупность информационных элементов, связанных между собой логическими связями (рис. 1), можно выделить основные нарушения:"),_c('br'),_vm._v(" — физической целостности (уничтожение, разрушение элементов);"),_c('br'),_vm._v(" — логической целостности (разрушение логических связей );"),_c('br'),_vm._v(" — содержания (изменение блоков информации, внешнее навязывание ложной информации);"),_c('br'),_vm._v(" — конфиденциальности (разрушение защиты, уменьшение степени защищенности информации), — прав собственности на информацию (несанкционированное копирование, использование)."),_c('br'),_vm._v(" С учетом этого для таких объектов систем угроза информационной безопасности представляет реальные или потенциально возможные действия или условия, приводящие к овладению конфиденциальной информацией, хищению, искажению, изменению, уничтожению ее и сведений о самой системе, а также к прямым материальным убыткам."),_c('br'),_vm._v(" Обобщая рассмотренные угрозы, можно выделить три наиболее выраженные для систем обработки информации:"),_c('br'),_vm._v(" 1) подверженность физическому искажению или уничтожению;"),_c('br'),_vm._v(" 2) возможность несанкционированной (случайной или злоумышленной) модификации;"),_c('br'),_vm._v(" 3) опасность несанкционированного (случайного или преднамеренного) получения информации лицами, для которых она не предназначалась."),_c('br'),_vm._v(" Кроме того, с точки зрения анализа процесса обработки информации выделяют такую угрозу, как блокирование доступа к обрабатываемой информации."),_c('br'),_vm._v(" Угрозы безопасности информации в современных системах ее обработки определяются умышленными (преднамеренные угрозы) и естественными (непреднамеренные угрозы) разрушающими и искажающими воздействиями внешней среды, надежностью функционирования средств обработки информации, а также преднамеренным корыстным воздействием несанкционированных пользователей, целями которых являются хищение, уничтожение, разрушение, несанкционированная модификация и использование обрабатываемой информации. При этом под умышленными, или преднамеренными, понимаются такие угрозы, которые обусловливаются злоумышленными действиями людей. Случайными, или естественными, являются угрозы, не зависящие от воли людей. В настоящее время принята следующая классификация угроз сохранности (целостности)"),_c('br'),_c('img',{attrs:{"src":"img/img-3.png"}}),_c('br'),_vm._v(" Под источником угроз понимается непосредственный исполнитель угрозы с точки зрения ее негативного воздействия на информацию. Источники можно разделить на следующие группы:"),_c('br'),_vm._v(" — люди;"),_c('br'),_vm._v(" — технические устройства;"),_c('br'),_vm._v(" — модели, алгоритмы, программы;"),_c('br'),_vm._v(" — технологические схемы обработки;"),_c('br'),_vm._v(" — внешняя среда"),_c('br'),_vm._v(" Предпосылки появления угроз Существуют следующие предпосылки, или причины, появления угроз:"),_c('br'),_vm._v(" — объективные (количественная или качественная недостаточность элементов системы) — не связанные непосредственно с деятельностью людей и вызывающие случайные по характеру происхождения угрозы;"),_c('br'),_vm._v(" — субъективные — непосредственно связанные с деятельностью человека и вызывающие как преднамеренные (деятельность разведок иностранных государств, промышленный шпионаж, деятельность уголовных элементов и недобросовестных сотрудников), так и непреднамеренные (плохое психофизиологическое состояние, недостаточная подготовка, низкий уровень знаний) угрозы информации."),_c('br'),_vm._v(" Перечисленные разновидности предпосылок интерпретируются следующим образом:"),_c('br'),_vm._v(" — количественная недостаточность — физическая не хватка одного или нескольких элементов системы обработки, вызывающая нарушения технологического процесса обработки или перегрузку имеющихся элементов;"),_c('br'),_vm._v(" — качественная недостаточность — несовершенство конструкции (организации) элементов системы, в силу чего может появляться возможность случайного или преднамеренного негативного воздействия на обрабатываемую или хранимую ин формацию;"),_c('br'),_vm._v(" — деятельность разведорганов иностранных государств — специально организуемая деятельность государственных органов разведки , профессионально ориентированных на добывание необходимой информации всеми доступными способами и средствами;"),_c('br'),_vm._v(" — промышленный шпионаж: — негласная деятельность отечественны х и зарубежных промышленных организаций (фирм), направленная на по лучение незаконным путем конфиденциальной информации, используемой для достижения промышленных, коммерческих, политических или подрывных целей;"),_c('br'),_vm._v(" — злоумышленные действия уголовных элементов — хищение информации, средств ее обработки или компьютерных программ в целях наживы или их разрушение в интересах конкурентов;"),_c('br'),_vm._v(" — плохое психофизиологическое состояние — постоянное или временное психофизиологическое состояние сотрудников, приводящее при определенных нестандартных внешних воздействиях к увеличению ошибок и сбоев в обслуживании систем обработки информации или непосредственно к разглашению конфиденциальной информации;"),_c('br'),_vm._v(" — недостаточная качественная подготовка сотрудников — уровень теоретической и практической подготовки персонала к выполнению задач по защите информации, недостаточная степень которого может привести к нарушению процесса функционирования системы защиты информации."),_c('br'),_vm._v(" В современной литературе и нормативно-правовых актах в области информационной безопасности можно встретить такую классификацию угроз информации, которая делит их на внутренние и внешние. Одной из наиболее принципиальных особенностей проблемы защиты информации является формирование полного множества угроз информации, потенциально возможных на объекте ее обработки. В самом деле, даже одна неучтенная угроза может в значительной мере снизить эффективность защиты."),_c('br'),_vm._v(" Возможные пути получения конфиденциальной информации"),_c('br'),_vm._v(" Анализ рассмотренных видов угроз позволяет сгруппировать их по двум основным областям:"),_c('br'),_vm._v(" 1) угрозы нарушения физической и логической целостности, а также содержания информации (несанкционированная модификация). Их можно объединить в причины нарушения целостности информации (ПНЦИ);"),_c('br'),_vm._v(" 2) угрозы, следствием которых может быть получение защищаемой информации (хищение или копирование) лицами, не имеющими на это полномочий, — в каналы несанкционированного получения информации (КНПИ)."),_c('br'),_vm._v(" Под действием рассмотренных выше угроз может произойти утечка защищаемой информации, то есть несанкционированное, неправомерное завладение соперником данной информацией и возможность использования ее в своих, в ущерб интересам собственника (владельца) информации, целях. При этом образуется канал утечки информации, под которым понимается физический путь от источника конфиденциальной информации к злоумышленнику. Для его возникновения необходимы определенные пространственные, энергетические и временные условия, а также соответствующие средства восприятия и фиксации информации на стороне злоумышленника ."),_c('br'),_vm._v(" . В зависимости от используемых соперником сил и средств для получения несанкционированного доступа к носителям защищаемой информации различают каналы агентурные, технические, легальные."),_c('br'),_vm._v(" Агентурные каналы утечки информации — это использование противником тайных агентов для получения несанкционированного доступа к носителям защищаемой информации. В случае использования агентами технических средств разведки (направленны х микрофонов, закладных устройств, миниатюрных видеокамер и др.) говорят о ведении агентурно-технической разведки."),_c('br'),_vm._v(" Технические каналы утечки информации — совокупность технических средств разведки, демаскирующих признаков объекта защиты и сигналов, несущих информацию об этих признаках. Эти каналы образуются без участи я человека в процессе обработки информации техническими средствами, а поэтому являются одними из наиболее опасны х и требуют отдельного рассмотрения."),_c('br'),_vm._v(" Легальные каналы утечки информации — это использование соперником открытых источников информации (литературы, периодических изданий и т. п), обратный инжиниринг, выведывание под благовидным предлогом информации у лиц, располагающих интересующей соперника информацией, и других возможностей. В основу классификации ПНЦИ положен показатель, характеризующий степень участия в этом процессе человека. В соответствии с таким подходом ПНЦИ делятся на два вида (объективные и субъективные) ."),_c('br'),_vm._v(" Для предотвращения возможной утечки конфиденциальной информации и нарушения ее целостности на объектах ее обработки ра